.ObtenerFoto #drop {
  border: 3px dashed blue;
  width: 400px;
  height: 200px;
  margin: 10px auto;
  background-color: lightyellow;
  text-align: center;
  font-size: 24px;
  padding: 10px;
}

.ObtenerFoto img {
  height: 120px;
  float: right;
}

.ObtenerFoto label {
  cursor: pointer;
  display: block;
  width: 100%;
  height: 100%;
  color: red;
}

.ObtenerFoto input {
  display: none;
}

.ObtenerFoto .info {
  color: #003DFF;
}

.ObtenerFoto .loader {
  width:32px;
  height: 32px;
  border: 5px solid #FFF;
  border-bottom-color: #003DFF;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
  margin-right: 10px;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}