.carrito {
    min-height: 600px;
    padding: 20px;

}

.carrito h1 {
    text-align: center;
}

/* ----- Maquetado de la tabla de productos ------- */
.carrito th,td {
    border: 1px solid black;
    width: 150px;
    height: 30px;
    padding: 20px;
}

.carrito table {
    border-collapse: collapse;
    margin: 5px auto;
}

.carrito th {
    text-transform: uppercase;
    background-color: #111;
    color: white;
}

.carrito h2 {
    text-align: center;
}


.carrito .centrar {
    text-align: center;
}


.carrito table button[id*="btnBorrar-"] {
    margin: 15px auto;
    display: block;

    width: 90%;
    height: 35px;
    border-radius: 5px;
    font-size: 18px;

    background-color: orangered;
    color: white;
}

.carrito table button[id*="btnDecrementar-"],
.carrito table button[id*="btnIncrementar-"] 
{
    margin: 15px 10px;

    width: 40px;
    height: 40px;
    border-radius: 5px;
    font-size: 18px;

    background-color: blue;
    color: white;
}


.carrito__borrar {
    margin: 15px auto;
    display: block;

    width: 100px;
    height: 40px;
    border-radius: 5px;
    font-size: 18px;

    background-color: red;
    color: white;
}

.carrito__pedir {
    margin: 15px auto;
    display: block;

    width: 100px;
    height: 40px;
    border-radius: 5px;
    font-size: 18px;

    background-color: green;
    color: white;
}
