.alta {
    background-color:#ddd;
    padding: 20px;
    height: auto;
}

.alta h1 {
    text-align: center;
}

/* ----- Maquetado del formulario de alta ------- */
.alta .alta-form {
    background-color: #b63625;
    width: 80%;
    margin: 20px auto;
    padding: 30px;
    color: white;
    border-radius: 20px;
    border: 1px dashed yellow;
}

.alta .input-group {
    text-align: center;
    padding: 3px;
}

.alta label {
    color: #badc58;
    text-transform: capitalize;
    display: block;
    margin-bottom: 5px;
}

.alta label[for="envio"] {
    display: inline-block;
    margin: 10px 0;
}


.alta input {
    width: 70%;
    height: 30px;
    border-radius: 5px;
    background-color:#95afc0;
}

.alta input[type="checkbox"] {
    width: 20px;
    height: 20px;
}

/* ----- Maquetado de la tabla de productos ------- */
.alta th,td {
    border: 1px solid black;
    width: 80px;
    height: 30px;
    padding: 10px;
}

.alta table {
    border-collapse: collapse;
    margin: 5px auto;
}

.alta th {
    text-transform: uppercase;
    background-color: #111;
    color: white;
}

.alta h2 {
    text-align: center;
}


.alta .centrar {
    text-align: center;
}


/* ------------------------------------------ */
.alta table button[id*="btnBorrar-"] {
    margin: 15px auto;
    display: block;

    width: 90%;
    height: 40px;
    border-radius: 5px;
    font-size: 18px;

    background-color: crimson;
    color: white;
}

.alta table button[id*="btnBorrar-"]:disabled {
    opacity: 0.5;
}

.alta table button[id*="btnEditar-"] {
    margin: 15px auto;
    display: block;

    width: 90%;
    height: 40px;
    border-radius: 5px;
    font-size: 18px;

    background-color: gold;
    color: #222;
}

.alta table button[id*="btnCancelar-"] {
    margin: 15px auto;
    display: block;

    width: 90%;
    height: 40px;
    border-radius: 5px;
    font-size: 16px;

    background-color: #222;
    color: gold;
}

.alta .btnAgregar {
    /* text-align: center; */
    margin: 15px auto;
    display: block;

    width: 40%;
    height: 35px;
    border-radius: 10px;
    font-size: 20px;

    background-color: #6ab04c;
    color: #130f40;
}

.alta .btnAgregar:disabled { opacity: 0.5; }

.alta .btnActualizar {
    /* text-align: center; */
    margin: 15px auto;
    display: block;

    width: 40%;
    height: 35px;
    border-radius: 10px;
    font-size: 20px;

    background-color: gold;
    color: #222;
}
.alta .btnActualizar:disabled { opacity: 0.5; }
