.inicio {
    height: auto;
    background-color: #689;
    padding: 25px;
    text-align: center;
}

.inicio .section-cards-container {
    display: flex;
    /* flex-wrap: nowrap; */
    flex-wrap: wrap;

    justify-content: space-evenly;
}

.inicio section img {
    width: 200px;
    margin-bottom: 20px;
}

.inicio section {
    border: 1px  dashed yellow;
    width: 350px;
    padding: 15px;
    background-color:#943;
    color: white;
    margin: 25px;
    border-radius: 15px;
}

.inicio section h3 {
    font-size: 24px;
    margin-bottom: 20px;
    text-decoration: underline;
    color: rgba(255,255,255,0.7)
}

.inicio .section-cards-header h1 {
    color: white;
    margin-bottom: 40px;
}

.inicio .section-cards-container p {
    /* text-align: left; */
}

.inicio section p {
    margin-top: 10px;
}

.inicio section button[id^="btnComprar-"] {
    margin: 30px auto 5px;
    display: block;

    width: 150px;
    height: 50px;
    border-radius: 5px;
    font-size: 20px;

    background-color: #6ab04c;
    color: #130f40;
}

.inicio section button[id^="btnComprar-"]:disabled {
    opacity: 0.5;
}
